import React, {Component, Fragment} from 'react';
import {Input, Button} from "antd";

class TranslateInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      texts: [],
    };
  }
  download = () => {
    const str = this.formatTrans(0);
    const blob = new Blob([str], {type: 'text/plain'});
    this.saveFile(blob, '翻译.txt');
  };
  formatTrans = (rev) => {
    let arr = JSON.parse(JSON.stringify(this.state.texts));
    if (rev) {
      arr.reverse();
    }
    return arr.map(obj => `${obj.time}  ${obj.text}`).join('\n');
  };
  /**
   * 保存文件
   */
  saveFile = (blob, filename) => {
    const saveData = (() => {
      const a = document.createElement('a');
      document.body.appendChild(a);
      a.style = 'display: none';
      return (b, fn) => {
        const url = window.URL.createObjectURL(b);
        a.href = url;
        a.download = fn;
        a.click();
        window.URL.revokeObjectURL(url);
      };
    })();
    saveData(blob, filename);
  };
  onPressEnter = (e) => {
    const { elapsed } = this.props;
    const value = e.target.value;
    const text = value.split('\n');
    const newText = {
      time: elapsed,
      text: text[text.length - 1],
    };
    this.setState(state => ({
      ...state,
      texts: state.texts.concat(newText),
    }));
  };
  render() {
    return (
      <Fragment>
        <tr>

          <th>翻译</th>
          <td>
            <div className="input">
              <Input.TextArea
                onPressEnter={this.onPressEnter}
              />
            </div>
          </td>
        </tr>

        <tr>
          <th>预览</th>
          <td>
            <div className="output">
              <pre>
                {this.formatTrans(1)}
              </pre>
            </div>
          </td>
        </tr>

        <tr>
          <th>下载</th>
          <td>
            <Button type="ghost" size="small" onClick={this.download}>点击下载翻译文档</Button>
          </td>
        </tr>
      </Fragment>

    );
  }
}

export default TranslateInput;
